@import "http://at.alicdn.com/t/font_269893_oig1r93k3lnb3xr.css";
$color-border: #E9E9E9;
$color-background: #F4F4F4;
$color-primary: #5D98F0;

html,body {
    min-width:1200px;
    min-height:100vh;
}
body {
    margin: 0;
    font: 14px/1.5 Arial, "Microsoft Yahei";
    display: flex;
    flex-direction: column;
    color:#566573;
    overflow-y:scroll;
}
a{
