}

.mr10{
    margin-right: 10px;
}
.mt20{
    margin-top: 20px;
}
.btn,.form-control{
    font-size: 14px;
}
.btn{
    cursor: pointer;
    &[disabled]{
        cursor:not-allowed;
        filter: grayscale(100%);
    }
}


