.main-section{
    flex:1;
    display: flex;
    >.container-fluid{
        flex:1;
        display: flex;
        padding: 0;
    }
    .main-row{
        display: flex;
        width: 100%;
    }
    .content-row{
        flex:1;
        position: relative;
    }

}

.main-row>.nav-row{
    border-right:1px solid $color-border;
    background-color: #EcF0F1;
    width: 205px;
    .nav-side{
        list-style:none;
        padding-left: 0;
        margin: 0;
        flex-direction: column;
        .nav-link{
            padding: 15px 15px;
            display: block;
            border-bottom:1px solid #dbdbdb;
            border-radius:0;
            color:inherit;
            text-decoration: none;
            .iconfont{
                display: none;
            }
            &:hover{
                color: #555;
            }
            &.active{
                background-color: #fff;
                color:$color-primary;
            }
            &:before{
                content: "";
                width: 5px;
                height: 5px;
                background-color: currentColor;
                opacity:0.5;
                display: inline-block;
                vertical-align:3px;
                margin-right: 3px;
            }
        }
    }
}

.main-row>.content-row{
    background-color: #fff;
    .breadcrumb{
        display: flex;
        border-bottom: 1px solid $color-border;
        border-radius: 0;
        padding: 15px 15px;
        background-color: $color-background;
        margin-bottom: 0;
        em{
            flex:1;
            font-style: normal;
            font-size: 14px;
        }
    }
    .sub-header{
        border-bottom:1px solid $color-border;
        padding:0 15px;
        margin-bottom: 10px;
        display: flex;
        align-items:center;
        min-height: 52px;
        h2{
            font-size: 18px;
            flex:1;
            display: flex;
            align-items:center;
            margin-bottom: 0;
            color:#2D3E4F;
            font-weight: bold;
            .small{
                font-size: 14px;
                font-weight: normal;
                color: #666;
                margin-left: 10px;
                border-left:1px solid currentColor;
                padding-left: 10px;
            }
        }
        .opt{
        }

    }
    .padding-wrapper{
        padding:0 15px 15px;
        >.nav-tab{
            margin: -10px -15px 10px;
        }
    }
}



.links-wrapper{
    a{
        margin-right: 10px;
        &:last-child{
            margin-right: 0px;
        }
    }
}
.buttons-wrapper{
    >.btn{
        margin-right: 10px;
        &:last-child{
            margin-right: 0;
        }
    }
    >.btn-group{
        margin-right: 10px;
    }
}
.has-required .col-form-label:before{
    content: "*";
    color:red;
    margin-right: 5px;
    display: inline-block;
    vertical-align: -2px;
}

.help-block{
    font-size: 12px;
    margin: 5px 0 0 0;
}

.has-danger .help-block{
    color:#D9534F;
}

.col-form-label{
    text-align: right;
    &:after{
        content: ":";
        margin-right: -5px;
        padding-left: 5px;
    }
    &.label-empty:after{
        content:none;
    }
}

.custom-control{
    margin-bottom: 0;
    min-height: 14px;
    margin-top: 3px;
    .custom-control-input{
        visibility: hidden;
        &:focus{
            outline-width:0;
        }
        &[disabled]~.custom-control-indicator{
            border-color: #CCC;
            cursor: not-allowed !important;
        }
    }
    .custom-control-indicator{
        top: 0;
    }
    .custom-control-description{
        margin-top: 1px;
        line-height: 14px;
    }
}

.custom-control{
}
.custom-checkbox .custom-control-input:disabled:checked~.custom-control-indicator{
    background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3E%3Cpath fill='#AAA' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3E%3C/svg%3E");
    cursor:not-allowed !important;
}
.custom-radio .custom-control-input:disabled:checked~.custom-control-indicator{
    background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='-4 -4 8 8'%3E%3Ccircle r='3' fill='#AAA'/%3E%3C/svg%3E")
}



