.table-center{
    th,td{
        text-align: center;
    }
}
.table-custom{
    thead>tr>th{
        background-color: $color-background;
        vertical-align: middle !important;
        border-right-color:#e1e1e1;
    }
    tbody>tr>th{
        vertical-align: middle !important;
        padding-top: 0;
        padding-bottom: 0;
        height: 40px;
    }
    tbody>tr>td{
        vertical-align: middle;
        border-right:none;
        padding-top: 0;
        padding-bottom: 0;
        height: 50px;
    }
    time,.small{
        font-size: 12px;
        span{
            display: block;
        }
    }
    .small{
        white-space:nowrap;
        overflow:hidden;
        /*
        max-width: 17em;
        */
        text-overflow:ellipsis;
    }
    tbody>tr:hover td[rowspan]{
        background-color: white;
    }
    tr.active,tr.active:hover{
        background-color: #ffffc7;
    }
    .form-control{
        padding-top: 4px;
        padding-bottom: 4px;
    }
    select.form-control{
        height: 25px !important;
        line-height: 25px !important;
        padding-top: 0px !important;
    }


    .align-top{
        vertical-align: top;
        padding-top: 20px;
    }
    .caption-top{
        caption-side: top;
    }
    .tr-collapse{
        border-top:1px solid red;
        td{
            padding: 10px;
        }
        &:hover{
            background-color: transparent;
        }
        .table-custom{
            margin-bottom: 0;
        }
    }
}

.table-bordered{
    tbody>tr>td{
        border-left:1px solid #ECEEEF;
    }
}
.table-nohover{
    tbody tr:hover{
        background-color: transparent;
    }
}

.table tbody+tbody{
    border-top-width:1px;
}

.table-modal-add{
    border-left:none !important;
    .form-control[type="number"]{
        display: inline-block;
        width: 65px;
        text-align:right;
    }
    tbody:after{
        content:"";
        display:table-row;
        border:none;
        height: 15px;
        margin-left: -3px;
        position: relative;
        left:-10px;
    }
    tbody:last-child:after{
        content:none;
    }
}


.table-sticky-custom{
    thead{
        position: -webkit-sticky;
        position: sticky;
        top: 0px;
        z-index: 3;
    }
}

.menu-wrapper{
    display: flex;
    margin-top: -10px;
    .nav-row{

        border-right:1px solid $color-border;
        .nav-side{
            list-style:none;
            padding-left: 0;
            margin: 0;
            margin-bottom: 15px;
            flex-direction: column;
            min-width: 205px;
            &.nav-collapse{
                /*
                min-width: 240px;
                 */
.ul-collapse{
    margin-left: 10px;
    margin-bottom: 15px;
    li{
        &>a{
            color:inherit;
        }
        padding:5px 0;
        &.active{
            list-style-type: disc;
        }
        &.active,&.active>a{
            color: $color-primary;
        }
    }
}
.nav-link::before{
    left: 25px;
    top: 20px;
    transition:all 500ms;
    transform:rotate(90deg);
    transform-origin: 0% 25%;
}
.nav-link.collapsed::before{
    left:20px;
    transform:rotate(0deg);
}
            }
            .nav-item{
                border-bottom:1px solid $color-border;
            }
            .nav-link{
                padding: 15px 15px;
                display: block;
                color:inherit;
                text-decoration: none;
                padding-left: 30px;
                position: relative;
                border-radius:0;
                .iconfont{
                    float: right;
                    font-size: 20px;
                    margin-top: -2px;
                }
                &:before{
                    content:"";
                    position: absolute;
                    top: calc(50% - 2px);
                    transform: translate(-100%,-50%);
                    width: 0;
                    height: 0px;
                    border-width:5px;
                    border-style: solid;
                    border-color: transparent transparent transparent currentColor;
                }
                &.active{
                    background-color: $color-background;
                    color:$color-primary;
                }
            }
        }
    }
}


.icon-done{
    color:#2ECC71;
}
.icon-close{
    color: #E74C3C;
}

.confirm-wrapper{
    background-color: #f4f4f4;
    padding:15px;
    margin: 20px 0;
    textarea{
        flex:1;
    }
}

.nav-tab{
    border-bottom:1px solid $color-border;
    .nav-item{
    }
    .nav-link{
        color:inherit;
        position: relative;
        padding: 15px 20px;
        &.active{
            color:$color-primary;
            background-color: transparent;
            &:after{
                content:"";
                width: 10px;
                height: 10px;
                position: absolute;
                bottom:0;
                left: 50%;
                transform: translate(-50%, 50%) rotate(-45deg);
                background-color: white;
                border:1px solid;
                border-color: $color-border $color-border transparent transparent;

            }
        }
    }
}


.card{
    border:none;
    .card-block{
        background-color: $color-background;
    }
    .card-title{
        font-size: 18px !important;
        margin-bottom: 0;
    }
    .iconfont{
        font-size: 40px;
        vertical-align: middle;
        margin-right: 10px;
    }
