.iconfont{
    font-size: inherit;
}

.icon-success,
.icon-i-success{
    color: green;
}
.icon-failed,
.icon-i-error{
    color: rgb(229, 47, 63);
}
.icon-alert,
.icon-i-alert{
    color: rgb(255, 184, 83);
}

.modal{
    .modal-body{
        max-height: 75vh;
        overflow: auto;
        min-width: 500px;
    }
    .modal-header{
        padding: 10px 15px;
        background-color: #2C3E50;
        color:white;
        .close{
            color:white;
            text-shadow: none;
        }

    }
    .modal-title{
        font-size:16px;
    }
    .modal-footer{
        justify-content: center;
    }
    .desc{
        width: 80%;
        margin: auto;
        text-align:justify;

    }

    /*
     * modal align center
     * */
