.occupation-page{
    background: #Fcfcfc;
    position: relative;
    &:after{
        content:none;
    }
    .login-footer{
        position: absolute;
        z-index: 2;
        bottom:0;
        width: 100%;
        text-align: center;
        color: rgba(255,255,255,.7);
        text-shadow:0 0 10px rgba(0,0,0,0.5);
        a{
            color:white;
        }
        p{
            margin: 5px 0;
            font-size: 12px;
        }
    }
    .page-banner{
        height: 200px;
        color:white;
        text-align: center;
        background-image: url(../images/bg-occupation.jpg);
        background-size: cover;
        background-position: top 70% center;
        line-height: 200px;
        img{
            width:700px;
        }
    }
    .page-footer{
        color:#666 !important;
        padding:20px 0 0 0;
        position: relative;
        position: absolute;
        bottom:0;
        text-shadow:none;
        background-color: #f3f3f3;
        a{
            color:#666;
        }
    }
    .occupation-wrapper{
        width:1000px;
        margin:0px auto 69px;
        padding:20px 0;
        border:1px solid #F5f5f5;
        background-color: #fff;
        border-top:0;
        border-bottom:0;
        padding:10px;
        .sub-header{
            display: flex;
            align-items:center;
            padding-bottom: 10px;
            margin-bottom: 10px;
            border-bottom:1px solid #DDD;
            h2{
                font-size: 18px;
                display: inline-block;
                margin-right: 10px;
                margin-bottom: 0;
            }
            .small{
                font-size: 14px;
                color:#999;
            }
            .opt{
                margin-left: auto;
            }
        }
        .list{
            >.cell{
                display: flex;
                border-bottom:1px solid $color-border;
                margin-bottom: 10px;
                padding-bottom: 10px;
                &:last-child{
                    margin-bottom: 0;
                    padding-bottom: 0;
                    border-bottom:none;
                }
                .header{
                    width:40px;
                    background-color: $color-primary;
                    color:white;
                    display: flex;
                    align-items:center;
                    justify-content:center;
                }
                .content{
                    flex:1;
                    align-items:flex-start;
                    >span{
                        background-color: #f0f0f0;
                        border:1px solid transparent;
                        margin: 0 0 0px 10px;
                        text-align: center;
                        width:120px;
                        height: 75px;
                        font-size:16px;
                        display: inline-flex;
                        justify-content:center;
                        align-items:center;
                        vertical-align:top;
                        margin-top: 10px;
                        cursor: pointer;
                        &:nth-child(-n+7){
                            margin-top: 0;
                        }
                        &:hover{
                            border-color:$color-primary;
                        }
                    }
                }
            }
        }
    }

}


.city-page{
    background: #Fcfcfc;
    position: relative;
    &:after{
        content:none;
    }
    .city-banner{
        height: 200px;
        color:white;
        text-align: center;
        background: url(../images/bg.jpg) 50% 600px;
        line-height: 200px;
        /*
        h2{
        font-size:50px;
        text-shadow:0 0 10px rgba(0,0,0,0.2);
        font-weight: bold;
        line-height: 200px;
        }
         */
        img{
            width:700px;
        }
    }
    .city-footer{
        color:#666 !important;
        padding:20px 0 0 0;
        position: relative;
        position: absolute;
        bottom:0;
        text-shadow:none;
        background-color: #f3f3f3;
        a{
            color:#666;
        }
    }

    .city-wrapper{
        width:1000px;
        margin:0px auto 69px;
        padding:20px 0;
        border:1px solid #F5f5f5;
        background-color: #fff;
        border-top:0;
        border-bottom:0;
        padding:10px;
        .sub-header{
            display: flex;
            align-items:center;
            padding-bottom: 10px;
            margin-bottom: 10px;
            border-bottom:1px solid #DDD;
            h2{
                font-size: 18px;
                display: inline-block;
                margin-right: 10px;
                margin-bottom: 0;
            }
            .small{
                font-size: 14px;
                color:#999;
            }
            .opt{
                margin-left: auto;
            }
        }
        .list{
            &:after{
                content:"";
                display: block;
                clear:both;
            }
        }

        dl{
            display: flex;
            align-items:center;
            flex-wrap:wrap;
            width: 45%;
            background-color: #f0f0f0;
            border-radius:10px;
            padding:20px 0;
            margin:15px 2.5%;
            position: relative;
            padding-left: 90px;
            height:100px;
            float:left;
            &:after{
                content:"";
                position: absolute;
                width: 10px;
                height:100%;
                background-color: #3496D9;
                right:0;
                border-radius:0 10px 10px 0;
            }
            &:before{
                content:"";
                position: absolute;
                width: 5px;
                height:60%;
                background-color: #256d9a;
                left:-5px;
                clip-path:polygon(0 5%, 100% 0, 100% 100%, 0 95%);
            }
            dt{
                position: absolute;
                left:-5px;
                display: inline-block;
                background-color: #3496D9;
                border-radius:0 30px 30px 0 / 30px;
                padding:16px 20px;
                color:white;
                box-shadow:3px 0 5px rgba(0,0,0,0.2);
            }
            dd{
                display: inline-block;
                font-size: 16px;
                margin-left: 30px;
                margin-bottom: 0;
            }
        }



    }
}


