        }
    }
    tr:hover td,
    tr:hover th
    {
        color:#669EE7 !important;
        text-decoration: underline;
        cursor: pointer;
    }
}
.dot{
    width: 14px;
    height: 14px;
    border-radius:50%;
    display: inline-block;
    vertical-align: -2px;
    margin-left: 15px;
    &.dot-not{
        background-color:#C9CACA;
    }
    &.dot-underway{
        background-color: #609AED;
    }
    &.dot-done{
        background-color: #9AC944;
    }
}


.timeline{
    margin-top: 20px;
    display: grid;
    grid-template-columns: repeat(5, 13%);
    grid-gap:60px 8.7%;
    .cell{
        border:1px solid #F4F4F4;
        display: inline-block;
        padding:5px 10px 5px 20px;
        background-color: #f4f4f4;
        position: relative;
        z-index: 2;
        cursor: pointer;
        &:after{
            position: absolute;
            left:calc(100% + 10px);
            top:50%;
            content:"";
            width:60%;
            height: 10px;
            background-color: #CCC;
            transform:translateY(-50%);
            clip-path:polygon(0 40%, 80% 40%, 80% 0, 100% 50%, 80% 100%, 80% 60%, 0 60%);
        }
        &:nth-child(6):after,
        &:nth-child(7):after,
        &:nth-child(8):after,
        &:nth-child(9):after,
        &:nth-child(10):after,
        &:nth-child(16):after,
        &:nth-child(17):after,
        &:nth-child(18):after,
        &:nth-child(19):after,
        &:nth-child(20):after
        {
            transform:rotate(180deg);
            transform-origin:0 0;
            left:-10px;
        }
        &:nth-child(5):after,
        &:nth-child(10):after,
        &:nth-child(15):after
        {
            transform:rotate(90deg);
            transform-origin:0 0;
            left: 50%;
            top:calc(100% + 10px);
            width:40px;
            z-index: 1;
        }
        &:last-child:after,
        &.omega:after{
            content:none;
        }
        .date{
        }
        .text{
            font-weight: bold;
            font-size: 16px;
        }
        .status{
            background-color: #C9CACA;
            width: 30px;
            height: 30px;
            border-radius:50%;
            display: block;
            position: absolute;
            clip-path:polygon(50% 0, 100% 0, 100% 100%, 50% 100%);
            left: 0;
            top:50%;
            transform:translate(-50%, -50%)
        }
        &.done {
            border-color:#9BC74E;
            .status{
                background-color: #9BC74E;
            }
        }
        &.active{
            border-color:#639CEA;
            .status{
                background-color: #639CEA;
            }

        }
        &.empty{
            visibility: hidden;
        }



        &:nth-child(6){
            order:10
        }
        &:nth-child(7){
            order:9
        }
        &:nth-child(8){
            order:8
        }
        &:nth-child(9){
            order:7
        }
        &:nth-child(10){
            order:6
        }
        &:nth-child(11){
            order:11
        }
        &:nth-child(12){
            order:12
        }
        &:nth-child(13){
            order:13
        }
        &:nth-child(14){
            order:14
        }
        &:nth-child(15){
            order:15
        }
        &:nth-child(16){
            order:20
        }
        &:nth-child(17){
            order:19
        }
        &:nth-child(18){
            order:18
        }
        &:nth-child(19){
            order:17
        }
        &:nth-child(20){
            order:16
        }
    }
}





